@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300&family=Roboto:ital,wght@0,100;0,300;1,100;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&family=Raleway:wght@300&family=Roboto:ital,wght@0,100;0,300;1,100;1,900&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,900&display=swap');
*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background-color: #d2dbdd;
}

.heading_style{
    padding: 30px 0 ;
    text-align: center;
     font-family: "Raleway", sans-serif; 
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    /* color: #868686; */
    background-color: #fff;
    font-size: 1.5em;
    margin-bottom: 30px;
}

.card{
    width:100px;
    height:auto;
}
.cards{
    margin: 6%;
    transition: all 0.4 cubic-bezier(0.175,0.885,0,1);
    background-color: #fff;
    width: 21.25%;
    border-radius: 12px;
    box-shadow: 0px 13px 10px rgba(0,0,0,0.1);
    float:left;
}
.card:hover{
    box-shadow: 0px 30px 18px -8px rgba(0,0,0,0.1);
    transform: scale(1.05,1.05);
}
.card_img{
    width: 100%;
    height: 235;
    border-top-left-radius:12px;
    border-top-right-radius:12px
}
.card_info{
    background-color: #fff;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    padding: 16px 24px 24px 24px;
    background-color: #fff;
}
